import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Bootstrapper from './Bootstrapper';
import API from "./Services/API";

const LaunchApp = () => ReactDOM.render(<App />, document.getElementById('app'));
function LaunchReact(empresa, error) {
    ReactDOM.render(<Bootstrapper empresa={empresa} error={error} launch={LaunchApp} />, document.getElementById('app'));
}

API.GetStoreData()
    .then(x => LaunchReact(x))
    .catch(x => LaunchReact(null, x));


