import React from "react";

export default function (Component, step) {
    return class extends React.Component {
        state = {
            render: true
        }
        constructor(props) {
            super(props);
            const moveToStep = props.onValidate(step);
            if (moveToStep >= 0) {
                props.history.push("/" + moveToStep);
                this.state.render = false;
            }
        }
        render() {
            if (!this.state.render) return null;
            return <Component stepIndex={step} {...this.props} />
        }
    }
}
