import React from 'react';
import { Paper, Stepper, Step, StepLabel, Button, CircularProgress, Typography } from '@material-ui/core';
import { Switch, Route, withRouter, HashRouter, Redirect, Prompt } from "react-router-dom";
import {
  ReceiptOutlined, PlaceOutlined, PersonOutlined, AddShoppingCartOutlined,
  Receipt, Place, Person, ShoppingCart, ArrowBack
} from '@material-ui/icons';

import API from "./Services/API";
import * as prefs from "./Services/Preferences";
import State from "./App.state";

import A from './Components/TakeOrder';
import B from './Components/ConfirmOrder';
import C from './Components/ContactData';
import D from './Components/Location';
import E from './Components/Done';
import WithValidate from './HOCs/WithValidate';
import ValidateInput from './Services/ValidateInput';

const TakeOrder = WithValidate(A, 0);
const ConfirmOrder = WithValidate(B, 1);
const ContactData = WithValidate(C, 2);
const Location = WithValidate(D, 3);
const Done = WithValidate(E, 4);

const STEPS = [
  { t: "Elige tus productos", icon: AddShoppingCartOutlined, currentIcon: ShoppingCart },
  { t: "Confirma tu pedido", icon: ReceiptOutlined, currentIcon: Receipt },
  { t: "Registra tus datos", icon: PersonOutlined, currentIcon: Person },
  { t: "Ingresa tu ubicación", icon: PlaceOutlined, currentIcon: Place }
];

class App extends React.Component {
  state = new State();
  constructor(props) {
    super(props);
    this.handleOptionOpened = this.handleOptionOpened.bind(this);
    this.handleItemChange = this.handleItemChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleInvoiceDataChange = this.handleInvoiceDataChange.bind(this);
    this.handlePositionChanged = this.handlePositionChanged.bind(this);

    this.handleGuardar = this.handleGuardar.bind(this);

    this.validateStep = this.validateStep.bind(this);
    this.handleValidateStep = this.handleValidateStep.bind(this);
    this.handleNextClick = this.handleNextClick.bind(this);

    this.state.contactData = prefs.Get("contactData") || this.state.contactData;
    this.state.invoiceData = prefs.Get("invoiceData") || this.state.invoiceData;
    this.state.direccion = prefs.Get("direccion") || this.state.direccion;
  }
  handleValidateStep(step) {
    // CUANDO SE MUEVE EL PEDIDO, POR EJEMPLO HACIA ATRAS, BORRO TODO Y COMIENZO DE 0
    if (!(window.location.href.indexOf("http://localhost") === 0)) {
      if (this.state.orderNumber) {
        const state = { ...new State(), loading: false, productList: [...this.state.productList] };
        state.contactData = prefs.Get("contactData") || state.contactData;
        state.invoiceData = prefs.Get("invoiceData") || state.invoiceData;
        state.direccion = prefs.Get("direccion") || state.direccion;
        this.setState(state);
        return 0;
      }
    }
    var prevstep = step - 1;
    var goToStep = undefined;
    if (step >= 0) {
      while (prevstep >= 0) {
        let msg = this.validateStep(prevstep);
        if (msg) goToStep = prevstep;
        prevstep--;
      }
    }
    return goToStep;
  }
  validateStep(step) {
    if (step === 0 || step === 1) {
      if (!this.state.items.length)
        return "Para continuar seleccione la menos un producto";
    }
    if (step === 1) {
      if (!this.state.forma_pago) return "Seleccione el Método de Pago";
    }
    if (step === 2 && this.state.contactData) {
      const x = this.state.contactData;
      if (!x.nombre) return "Agregue un nombre de contacto";
      if (!x.telefono) return "Agregue un telefono de contacto";
      let msg = ValidateInput.tel(x.telefono);
      if (msg) return msg;
      msg = ValidateInput.email(x.email);
      if (msg) return msg;
    }
    if (step === 3 && this.state.direccion) {
      const x = this.state.direccion;
      if (!x.position) return "Confirme su ubicación en el mapa.";
      if (!x.numero) return "Agregue un número de casa/local de la dirección de entrega.";
      if (!x.referencia) return "Agregue la referencia de la dirección de entrega.";
    }
    return;
  }
  componentDidMount() {
    API.GetStoreData().then(x =>
      this.setState({ ...x })
    ).then(_ =>
      API.GetProducts().then(products => {
        let productList = products.map(item => {
          var pvpWithTaxes = 0;
          if (item.pvp) pvpWithTaxes = item.pvp;
          if (item.pagaIva) pvpWithTaxes += (item.pvp * 12 / 100);
          item.pvpWithTaxes = pvpWithTaxes;
          return item;
        });
        this.setState({
          productList,
          loading: false
        }, () => console.info("Se han cargado los productos al estado"))
      })
    ).catch(ex => {
      console.dir(ex);
      if (ex.status === 404) return this.setState({ loading: false, products: [] });
      this.setState({ loading: false, error: { ...ex } });
    });
  }
  handleOptionOpened(index) {
    // esto de errGuardar lo pongo en caso de que 
    // el pedido se haya guardado, y haya fallado
    // y se decide ir atras a ver qué está mal con el pedido.
    // entonces errGuardar vuelve a NULL
    this.setState({ activeStep: index, errGuardar: null });
  }
  handleInputChange(event) {
    const { name, value } = event.currentTarget;
    this.setState(prev => {
      const state = { ...prev };
      if (name.indexOf(".") >= 0) {
        const levels = name.split(".");
        const key = levels.pop();
        let reference = state;
        for (let i = 0; i < levels.length; i++) {
          reference = reference[levels[i]];
        }
        reference[key] = value;
      } else {
        state[name] = value;
      }
      return state;
    });
  }
  handleItemChange(item) {
    let PrevItem = this.state.items.find(x => x.id === item.id);
    if (!PrevItem && item.cantidad) {
      const items = [...this.state.items];
      items.push(item);
      return this.setState({ items });
    }
    if (PrevItem) {
      const items = [...this.state.items];
      let PrevIndex = items.indexOf(PrevItem);
      if (item.cantidad) {
        items[PrevIndex] = item;
      } else {
        items.splice(PrevIndex, 1);
      }
      this.setState({ items });
    }
  }
  handleInvoiceDataChange(invoiceData) {
    this.setState({ invoiceData });
    if (invoiceData) {
      prefs.Set("invoiceData", JSON.stringify(invoiceData));
    } else {
      prefs.Set("invoiceData");
    }
  }
  handlePositionChanged(new_position) {
    this.setState(prev => {
      const state = { ...prev };
      state.direccion = { ...state.direccion };
      state.direccion.position = new_position;
      return state;
    });
  }
  handleNextClick() {
    const { contactData, direccion, activeStep } = this.state;
    if (activeStep === 2 && contactData)
      prefs.Set("contactData", JSON.stringify(contactData));

    if (activeStep === 3 && direccion)
      prefs.Set("direccion", JSON.stringify(direccion));
  }
  handleGuardar(index) {
    this.handleOptionOpened(index);
    this.setState({ loading: "guardar", errGuardar: null });

    return API.SaveOrder(this.state).then(order => {
      this.setState({ loading: false, orderNumber: order.numero, errGuardar: null });
    }).catch(ex => {
      this.setState({ loading: false, errGuardar: ex });
    });
  }
  render() {
    const { activeStep, productList, loading, error,
      items, forma_pago, nota,
      invoiceData, contactData,
      direccion,
      errGuardar, orderNumber, ws_phone, ws_msg
    } = this.state;
    return (
      <HashRouter>
        <Paper style={{
          maxHeight: "100%",
          minHeight: "100%",
          margin: "auto",
          maxWidth: 700,
          display: "flex",
          flexDirection: "column"
        }}>

          <Stepper className="hide-on-min-height" alternativeLabel activeStep={activeStep}>
            {STEPS.map((x, i) => generateStep(x, i, activeStep))}
          </Stepper>
          {loading
            ? null
            : <Typography variant="h6" className="show-on-min-height" style={{
              textAlign: "center",
              textTransform: "uppercase",
              background: "#3f51b5",
              color: "white"
            }}>
              {(STEPS[activeStep] || {}).t}
            </Typography>}

          <div style={{ flex: 1, overflow: "hidden", display: "flex", flexDirection: "column" }}>
            {error
              ? (<div style={{ flex: 1, textAlign: "center", padding: 20 }}>
                <Typography variant="h6" color="secondary">{error.message}</Typography>
                <Button fullWidth style={{ marginTop: 20, marginBottom: 20 }} onClick={x => window.location.reload()} variant="outlined" color="primary">
                  REINTENTAR
                  </Button>
              </div>)
              : (loading === "data"
                ? (<div style={{ flex: 1, textAlign: "center", padding: 20 }}>
                  <CircularProgress size={40} />
                  <Typography color="primary" variant="h6">Obteniendo información de la Tienda...</Typography>
                </div>)
                : (<React.Fragment>
                  {loading
                    ? <Prompt message="Hay un proceso en progreso, no deberia salir hasta que se complete. Está seguro de salir?" />
                    : null}< Switch >
                    <Route path="/0"
                      render={props => <TakeOrder
                        {...props}
                        onValidate={this.handleValidateStep}
                        onMounted={this.handleOptionOpened}
                        onItemChange={this.handleItemChange}
                        choosenItems={items}
                        productList={productList}
                      />}
                    />
                    <Route path="/1"
                      render={props => <ConfirmOrder
                        {...props}
                        productList={productList}
                        onValidate={this.handleValidateStep}
                        onMounted={this.handleOptionOpened}
                        onItemChange={this.handleItemChange}
                        onInputChange={this.handleInputChange}
                        items={items}
                        nota={nota}
                        forma_pago={forma_pago}
                      />}
                    />
                    <Route path="/2"
                      render={props =>
                        <ContactData
                          {...props}
                          onValidate={this.handleValidateStep}
                          invoiceData={invoiceData}
                          data={contactData}
                          onInputChange={this.handleInputChange}
                          onInvoiceDataChange={this.handleInvoiceDataChange}
                          onMounted={this.handleOptionOpened}
                        />}
                    />
                    <Route path="/3" render={props =>
                      (<Location
                        {...props}
                        onValidate={this.handleValidateStep}
                        API_KEY={this.state.G_M_K}
                        onMounted={this.handleOptionOpened}
                        onPositionChanged={this.handlePositionChanged}
                        direccion={direccion} onInputChange={this.handleInputChange} />)} />
                    <Route path="/4"
                      render={props => (<Done
                        {...props}
                        ws_msg={ws_msg}
                        ws_phone={ws_phone}
                        contactData={contactData}
                        orderNumber={orderNumber}
                        onValidate={this.handleValidateStep}
                        onMounted={this.handleGuardar}
                        loading={loading}
                        error={errGuardar}
                        saved={Boolean(orderNumber)}
                      />)} />
                    <Redirect exact from="/" to="/0"></Redirect>
                    <Redirect exact from="" to="/0"></Redirect>
                  </Switch >
                  {STEPS.length === this.state.activeStep
                    ? null
                    : <RouterButton
                      onClick={this.handleNextClick}
                      disabled={Boolean(this.validateStep(this.state.activeStep))}
                      activeStep={this.state.activeStep}
                      errMsg={this.validateStep(this.state.activeStep)}
                    />}
                </React.Fragment>))}
          </div>
        </Paper>
      </HashRouter >
    );
  }
}
export default App;

const RouterButton = withRouter(NextButton);

function NextButton(props) {
  function fn() {
    if (props.errMsg) {
      alert(props.errMsg);
    } else {
      props.history.push("/" + (props.activeStep + 1));
    }
    props.onClick();
  }
  function back() {
    props.history.push("/" + (props.activeStep - 1));
  }
  return (<div style={{ width: "calct(100% - 20px)", display: "flex", flexDirection: "row", textAlign: "center", margin: 10, minHeight: 40 }}>
    {props.activeStep === 0
      ? null
      : <Button onClick={back} title="Regresar" color="secondary" variant="outlined">
        <ArrowBack />
      </Button>}
    <Button
      onClick={fn}
      disabled={props.disabled}
      color="primary"
      type="button"
      variant="outlined"
      style={{ flex: 1 }}
      size="large">
      CONTINUAR
  </Button>
  </div>)
}

function generateStep(item, index, current) {
  let data = {};
  if (current === index) data = { icon: <item.currentIcon /> };
  if (index > current) data = { icon: <item.icon /> };
  return (<Step key={index} >
    <StepLabel {...data} >{item.t}</StepLabel>
  </Step >)
}