import React from "react";
import { Typography, Button } from "@material-ui/core";
import { MyLocation, LocationDisabled } from "@material-ui/icons";

// aqui cargo todo lo relacionado a la información inicial antes de comenzar!
export default class extends React.Component {
    constructor(props) {
        super(props);
        const { empresa, error } = props;
        if (empresa) {
            this.state.logoSrc = empresa.logoSrc;
            this.state.slogan = empresa.slogan;
            this.state.titulo = empresa.titulo;
        }
        if (error) {
            if (error.status === 404) {
                this.state.error = "La tienda que buscas no existe. Revisa tu enlace.";
                this.state._error = "Ingresaste a: " + window.location.href;
            } else {
                this.state.error = error.message;
                this.state.code = error.code || error.status;
                this.state._error = error.statusText;
            }
        }
    }
    state = { begin: true }
    begin = () => {
        this.setState({ begin: false });
        if (!navigator.geolocation) return this.setState({
            error: "Tu navegador no soporta la función de Ubicación."
        });
        navigator.geolocation.getCurrentPosition(position => {
            window["lat"] = position.coords.latitude;
            window["lon"] = position.coords.longitude;
            this.props.launch();
        }, error => {
            if (error.code === error.PERMISSION_DENIED) {
                this.setState({
                    error: "No se ha podido establecer tu ubicación. Permitenos acceder a tu ubicación para tener una mejor experiencia.",
                    _error: error.message, code: error.code
                })
            } else if (error.code === error.POSITION_UNAVAILABLE) {
                this.setState({
                    error: "Tu navegador no nos permite encontrar tu ubicación.",
                    _error: error.message, code: error.code
                })
            } else {
                this.setState({ error: error.message, code: error.code });
            }
        });
    }
    render() {
        const { logoSrc, slogan, titulo, error, _error, begin } = this.state;
        return (<div style={{ width: "calc(100% - 20px)", maxWidth: 600, textAlign: "center", paddingTop: 30, margin: "auto" }}>
            {
                begin ? (
                    error ?
                        (<React.Fragment>
                            <Typography color="secondary" variant="h6">{error}</Typography>
                            <Typography color="secondary" variant="subtitle2">{_error}</Typography>
                        </React.Fragment>)
                        : <React.Fragment>
                            {logoSrc && <img src={logoSrc} style={{ maxWidth: "100%", maxHeight: 350, marginTop: 10, marginBottom: 10 }} />}
                            <Typography variant="h6">{titulo || "Cargando..."}</Typography>
                            {slogan && <Typography variant="subtitle2">{slogan}</Typography>}
                            {(titulo || logoSrc)
                                && (<Button color="primary" onClick={this.begin} fullWidth variant="outlined" style={{ marginTop: 20 }} >EMPECEMOS!</Button>)}
                        </React.Fragment >)
                    : (error
                        ? (<div style={{ width: "100%" }}>
                            <LocationDisabled fontSize="large" />
                            <Typography variant="body2">{error}</Typography>
                            <Typography variant="body1">{_error}</Typography>
                            <Button
                                style={{ marginTop: 20, wordWrap: "break-word" }}
                                onClick={() => this.props.launch()}
                                variant="outlined"
                                color="primary"
                            >ENTRAR DE TODAS FORMAS</Button>
                        </div>) : (<div style={{ width: "100%" }}>
                            <Typography variant="h5">
                                Primero necesitamos que nos permitas acceder a tu ubicación
                    </Typography>
                            <MyLocation fontSize="large" />
                        </div>))
            }
        </div>)
    }
}