import React from "react";
import { Typography, TextField, List, ListItem, ListItemText, ListItemSecondaryAction } from "@material-ui/core";
import { Route } from "react-router-dom";
import ProductDetails from "./ProductDetails";
import Totales from "../Services/Totales";

export default class extends React.PureComponent {
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepIndex);
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(event) {
        const id = event.currentTarget.id;
        this.props.history.push(this.props.match.url + "/view-product/" + id);
    }
    render() {
        console.log("ConfirmOrder rendered");
        const { props } = this;
        return (<div style={{ paddingLeft: 10, paddingRight: 10, overflow: "auto", flex: 1 }}>
            <List style={{ marginBottom: 10 }} dense={true}>
                {(props.items).map(x => generateRowItem(x, this.handleClick))}
                {generateTotals(props.items)}
            </List>
            <div style={{ width: "100%", textAlign: "center" }}>
                <TextField
                    label="Agrega una nota a tu pedido"
                    placeholder="Ej. Sin cubiertos, sin salas extra, etc."
                    multiline
                    name="nota"
                    inputProps={{ maxLength: 200 }}
                    value={props.nota}
                    onChange={props.onInputChange}
                    rows={2}
                    style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                />

                <TextField
                    label="Selecciona tu método de pago"
                    select
                    value={props.forma_pago}
                    name="forma_pago"
                    onChange={props.onInputChange}
                    variant="outlined"
                    style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                    SelectProps={{ native: true }}
                >
                    <option value={1}>Efectivo</option>
                    <option value={2}>Tarjeta de Crédito/Debito</option>
                </TextField>
            </div>
            <Route path={props.match.path + "/view-product/:id"} >
                <ProductDetails productList={props.productList} choosenItems={props.items || []} onDone={props.onItemChange} />
            </Route>
        </div>)
    }
}

function generateRowItem(data, onClick) {
    return (
        <ListItem key={data.id} button onClick={onClick} id={data.id}>
            <ListItemText primary={data.nombre} secondary={data.nota || ""} />
            <ListItemSecondaryAction>
                <Typography style={{ margin: 0, padding: 0 }} variant="h6">x{data.cantidad}</Typography>
            </ListItemSecondaryAction>
        </ListItem>)
}
function generateTotals(items) {
    const totals = Totales(items);
    return (<React.Fragment>
        <ListItem>
            <ListItemText primary={<Typography variant="h5" style={{ margin: 0, padding: 0 }}>
                {items.length + " ITEMS"}
            </Typography>} />
            <ListItemSecondaryAction>
                <Typography style={{ margin: 0, padding: 0 }} variant="h5">
                    ${totals.total.toFixed(2)}
                </Typography>
            </ListItemSecondaryAction>
        </ListItem>
    </React.Fragment>)
}

