import React from "react";
import { Typography, GridList, GridListTile, GridListTileBar } from "@material-ui/core";
import ProductDetails from "./ProductDetails";
import { Route } from "react-router-dom";

const styles = {
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden'
    },
    gridList: {
        flexWrap: 'nowrap',
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    }
};

export default class extends React.PureComponent {
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepIndex);
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(event) {
        this.props.history.push(this.props.match.path + "/view-product/" + event.currentTarget.id);
    }
    render() {
        console.log("TakeOrder rendered");
        const { productList, style, choosenItems, match, onItemChange } = this.props;
        const products = productList.reduce((prev, current) => {
            let exists = prev.find(x => x.group === current.grupo);
            if (!exists) {
                exists = {
                    group: current.grupo,
                    items: []
                }
                prev.push(exists);
            }
            exists.items.push(current);
            return prev;
        }, []);

        if (!(products && products.length)) {
            return (<div style={{ ...style, textAlign: "center" }}>
                <Typography variant="h6">Esta tienda no tiene productos disponibles. Intenta nuevamente después.</Typography>
            </div>)
        }
        return (<div style={{ ...style, textAlign: "left", overflowX: "hidden", overflowY: "auto" }}>
            {products.map((p, index) =>
                (<Carousel key={index}
                    onClick={this.handleClick}
                    style={{ marginTop: 10, marginBottom: 10 }}
                    title={p.group}
                    items={p.items}
                    choosenItems={choosenItems}
                />))}
            <Route path={match.path + "/view-product/:id"} >
                <ProductDetails
                    choosenItems={choosenItems || []}
                    onDone={onItemChange}
                    productList={productList}
                />
            </Route>
        </div>);
    }
}
function Carousel(props) {
    return (<div style={{ ...props.style }}>
        <Typography variant="h6" style={{ marginLeft: 10 }}>{props.title}</Typography>
        <GridList cols={2.5} style={styles.gridList}>
            {props.items.map((item) => {
                const found = (props.choosenItems || []).find(x => x.id === item.id);
                return generateCarouselItem(item, found && found.cantidad, props.onClick);
            })}
        </GridList>
    </div>)
}
function generateCarouselItem(item, cantidad, onClick) {
    const TileBar = {
        title: item.nombre,
        actionPosition: "right",
        subtitle: "$ " + item.pvpWithTaxes.toFixed(2)
    }
    if (cantidad) {
        TileBar.actionIcon = <Typography variant="body2" style={{ color: "#fff", marginRight: 5 }} >x{cantidad}</Typography>;
    }
    const Tile = {
        cursor: "pointer"
    };
    if (cantidad) {
        Tile.border = "3px dashed #f50057";
    }
    return (<GridListTile style={Tile} key={item.id} onClick={onClick} id={item.id} color="inherit">
        <img src={item.imgSrc} alt={item.nombre} />
        <GridListTileBar {...TileBar} />
    </GridListTile>);
}