import React from "react";
import { TextField, Typography, Button } from "@material-ui/core";
import { withGoogleMap, GoogleMap, withScriptjs } from "react-google-maps";
import SearchBox from "react-google-maps/lib/components/places/SearchBox";
import { MarkerWithLabel } from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { GpsFixed } from "@material-ui/icons";
import * as prefs from "../Services/Preferences";

const refs = {};

const STORE_LATLNG = {
    lat: -0.263496, lng: -78.488272
}
export default class extends React.PureComponent {
    state = {
        loading: false,
        defaultCenter: {},
        latLng: null,
        defaultZoom: 12,
        bounds: null,
        enableLocationBtn: false
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepIndex);
        this.handleMapClick = this.handleMapClick.bind(this);
        this.handlePlacesChanged = this.handlePlacesChanged.bind(this);
        this.handleSearchBoxMounted = this.handleSearchBoxMounted.bind(this);
        this.handleMapMounted = this.handleMapMounted.bind(this);
        this.handleBoundsChanged = this.handleBoundsChanged.bind(this);
        this.handleLocationClick = this.handleLocationClick.bind(this);
    }
    handleSearchBoxMounted(ref) {
        refs.searchBox = ref;
    }
    handleMapMounted(ref) {
        refs.map = ref;
    }
    handleBoundsChanged() {
        this.setState({ bounds: refs.map.getBounds() });
    }
    handlePlacesChanged() {
        const places = refs.searchBox.getPlaces();
        const bounds = new window.google.maps.LatLngBounds();

        places.forEach(place => {
            if (place.geometry.viewport) {
                bounds.union(place.geometry.viewport)
            } else {
                bounds.extend(place.geometry.location)
            }
        });
        const placeLocations = places.map(place => ({
            position: place.geometry.location,
            name: place.name
        }));
        let placeLoc = this.state.defaultCenter;
        if (placeLocations && placeLocations.length) {
            placeLoc = placeLocations[0];
        }
        this.setState({
            center: placeLoc.position
        });
        this.props.onPositionChanged({
            lat: placeLoc.position.lat(),
            lng: placeLoc.position.lng(),
            name: placeLoc.name
        });
    }
    componentWillMount() {
        const { direccion } = this.props;
        // prev stored
        if (direccion && direccion.position) {
            this.setState({
                center: { ...direccion.position },
                defaultZoom: 17
            });
            if (navigator.permissions) {
                navigator.permissions.query({ name: 'geolocation' })
                    .then(value => {
                        if (value.state === "granted") {
                            this.setState({ enableLocationBtn: true });
                        }
                    });
            }
            return;
        }
        if (!navigator.geolocation) return;
        navigator.geolocation.getCurrentPosition(position => {
            const latLng = {
                lat: position.coords.latitude,
                lng: position.coords.longitude,
                name: "Ubicación Actual"
            };
            this.setState({
                defaultCenter: { ...latLng, isCurrentLocation: true },
                defaultZoom: 17,
                loading: false,
                enableLocationBtn: true
            });
            this.props.onPositionChanged(latLng);
        }, err => {
            this.setState({
                loading: false,
                defaultCenter: { ...STORE_LATLNG },
                markerName: "",
                enableLocationBtn: false
            });
        })
    }
    handleMapClick(a) {
        this.props.onPositionChanged({ lat: a.latLng.lat(), lng: a.latLng.lng(), name: "" });
    }
    handleLocationClick() {
        navigator.geolocation.getCurrentPosition(position => {
            const center = {
                lat: position.coords.latitude,
                lng: position.coords.longitude,
                name: "Ubicación Actual"
            }
            this.props.onPositionChanged(center);
            this.setState({ center });
        }, err => {
            const state = { center: { ...this.state.defaultCenter } };
            if (state.center.isCurrentLocation) {
                delete state.center.isCurrentLocation;
                this.props.onPositionChanged(state.center);
            }
            alert("No pudimos obtener tu ubicación " + err.message);
            this.setState(state);
        });
    }
    render() {
        const { direccion, onInputChange, API_KEY } = this.props;
        const { defaultCenter, defaultZoom, center, bounds, enableLocationBtn } = this.state;
        return (<div style={{ overflow: "auto", paddingLeft: 10, paddingRight: 10, flex: 1 }}>
            <Map
                googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${API_KEY || ""}&v=3.exp&libraries=geometry,drawing,places`}
                //googleMapURL="https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places"
                containerElement={<div style={{ height: "300px" }} />}
                loadingElement={<div style={{ height: `100%` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                onClick={this.handleMapClick}
                defaultZoom={defaultZoom}
                onPlacesChanged={this.handlePlacesChanged}
                onSearchBoxMounted={this.handleSearchBoxMounted}
                onMapMounted={this.handleMapMounted}
                marker={direccion.position}
                center={center || defaultCenter}
                onBoundsChanged={this.handleBoundsChanged}
                searchBoxBounds={bounds}
                onLocationClick={this.handleLocationClick}
                enableLocationBtn={enableLocationBtn}
            />
            <TextField
                name="direccion.numero"
                value={direccion.numero}
                onChange={onInputChange}
                inputProps={{ maxLength: 100 }}
                label="Numero*"
                placeholder="Ej. Dpto 101, Oficina 304, Manzana 10 Villa 4, etc."
                fullWidth
            />

            <TextField
                name="direccion.referencia"
                value={direccion.referencia}
                onChange={onInputChange}
                inputProps={{ maxLength: 200 }}
                label="Referencia*"
                placeholder="Ej. Al lado de la panaderia 'El Pan de Cada Dia'"
                fullWidth
            />

        </div>)
    }
}

const Map = withScriptjs(withGoogleMap(function Map(props) {
    function changeFn(event) {
        event.preventDefault();
        event.stopPropagation();
    }
    const { google } = window;
    return (<GoogleMap
        {...props}
        ref={props.onMapMounted}
        options={{
            disableDefaultUI: true,
            zoomControl: true,
            clickableIcons: false
        }}
    >
        <SearchBox
            bounds={props.searchBoxBounds}
            onPlacesChanged={props.onPlacesChanged}
            controlPosition={google.maps.ControlPosition.TOP_LEFT}
            ref={props.onSearchBoxMounted}>
            <div style={{ display: "flex", flexDirection: "row", width: "calc(100% - 10px)", margin: 5 }}>
                <input
                    type="text"
                    onChange={changeFn}
                    placeholder="Busca tu dirección aqui"
                    style={{
                        flex: 1,
                        boxSizing: `border-box`,
                        border: `1px solid transparent`,
                        height: 40,
                        padding: `0 10px`,
                        borderRadius: `3px`,
                        boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,
                        fontSize: `14px`,
                        outline: `none`,
                        textOverflow: `ellipses`
                    }}
                />
                <Button style={{
                    maxHeight: 40,
                    padding: 5,
                    minWidth: 40,
                    background: "#fff",
                    boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`
                }} variant="outlined"
                    disabled={!props.enableLocationBtn}
                    onClick={props.onLocationClick}>
                    <GpsFixed />
                </Button>
            </div>
        </SearchBox>
        {props.marker
            ? (<MarkerWithLabel
                x labelAnchor={new window.google.maps.Point(0, 0)}
                position={props.marker}
                labelStyle={{ background: "#fff", border: "1px solid grey", padding: "10px" }}
                labelVisible={Boolean(props.marker.name)}
            >
                <Typography>{props.marker.name}</Typography>
            </MarkerWithLabel>)
            : null}
    </GoogleMap>)
}));